<template>
  <div id="comment-module">
    <div class="module-title">
      <span class="title">{{ total > 10 ? `(${total})` : '' }}</span>
      <div class="divider-wrap">
        <div class="divider"></div>
      </div>
    </div>
    <div class="input-module">
      <div class="creator">
        <a v-if="user.id" :href="`user/${user.id}`" class="user-face">
          <div class="avatar-wrap">
            <img class="avatar" :src="user.avatar" alt="" />
          </div>
        </a>
        <a v-else @click="showLoginModal" class="user-face">
          <div class="avatar-wrap">
            <img class="avatar" src="@/assets/noavatar.png" alt="" />
          </div>
        </a>
      </div>
      <div class="editor-container">
        <CommentEditor @addComment="addComment" :commentable-id="commentableId" :type="type" />
      </div>
    </div>
    <Skeleton v-if="loading" type="comments" />
    <div v-else class="comments-wrap">
      <div v-if="data?.length > 0" class="comment-list">
        <CommentItem v-for="comment in data" :key="comment.id" :comment="comment" @deleteComment="refetch">
          <div class="division"></div>
        </CommentItem>
      </div>
      <div v-else class="comments-empty">
        <Empty type="comment" description="" />
      </div>
    </div>
    <div class="comments-pagination" v-if="total > 10">
      <el-pagination
        background
        :page-size="10"
        :total="total"
        layout="prev, pager, next"
        :hide-on-single-page="true"
        @current-change="onPageChange"
      />
    </div>
  </div>
</template>

<script lang="ts">
import { toRefs, ref } from 'vue';
import { useApolloClient, useQuery, useResult } from '@vue/apollo-composable';
import { COMMENTS_QUERY } from '@/graphql';
import CommentItem from './CommentItem.vue';
import CommentEditor from './CommentEditor.vue';
import LoadingSpinner from '../LoadingSpinner.vue';
import Skeleton from '../Skeleton.vue';
import Empty from '../Empty.vue';

export default {
  components: {
    CommentItem,
    CommentEditor,
    LoadingSpinner,
    Skeleton,
    Empty
  },
  props: {
    commentableId: {
      type: Number
    },
    type: {
      type: String,
      default: () => {
        return 'movies';
      }
    }
  },
  setup(props) {
    const { commentableId, type } = toRefs(props);
    const currentPage = ref(1);
    function onPageChange(page: number) {
      currentPage.value = page;
    }

    const { result, loading, refetch } = useQuery(
      COMMENTS_QUERY,
      {
        type,
        id: commentableId,
        page: currentPage
      },
      { fetchPolicy: 'network-only' }
    );
    const data = useResult(result, {}, (data: any) => data?.comments?.data);
    const total = useResult(result, {}, (data: any) => data?.comments?.paginatorInfo?.total);

    const { resolveClient } = useApolloClient();
    function addComment(comment) {
      const client = resolveClient();
      const data = client.readQuery({
        query: COMMENTS_QUERY,
        variables: { type: type.value, id: commentableId.value, page: currentPage.value }
      });
      const commentsdata = [...data.comments.data];
      const paginatorInfo = { ...data.comments.paginatorInfo };
      if (commentsdata.length > 0) {
        commentsdata.splice(9, 1);
      }
      commentsdata.unshift(comment);
      client.writeQuery({
        query: COMMENTS_QUERY,
        variables: { type: type.value, id: commentableId.value, page: currentPage.value },
        data: {
          ...data,
          comments: Object.assign({}, data.comments, {
            data: commentsdata,
            paginatorInfo: {
              ...paginatorInfo,
              total: paginatorInfo.total + 1
            }
          })
        }
      });
    }

    return {
      loading,
      data,
      total,
      onPageChange,
      refetch,
      addComment
    };
  },
  computed: {
    user() {
      return this.$store.state.user.user;
    }
  },
  methods: {
    showLoginModal() {
      this.$bus.emit('SHOW_LOGIN_MODAL');
    }
  }
};
</script>

