<template>
  <div id="dplayer"></div>
</template>
<script>
import DPlayer from 'dplayer';
import moment from '@/common/moment';

export default {
  props: ['source', 'progress'],
  mounted() {
    this.playerConstructor();
    this.playerEventListener();
  },
  beforeDestroy() {
    if (this.player) {
      this.player.destroy();
    }
  },
  watch: {
    source(val, pre) {
      if (this.player) {
        this.player.switchVideo({
          url: val.url,
          type: 'auto'
        });
        this.player.play();
      }
    }
  },
  methods: {
    //
    playerConstructor() {
      const options = {
        container: document.getElementById('dplayer'),
        preload: false,
        autoplay: true,
        screenshot: true,
        video: {
          url: this.source.url,
          type: 'auto'
        }
      };
      this.player = new DPlayer(options);
    },
    // 
    playerEventListener() {
      this.player.on('loadeddata', () => {
        const duration = moment.format(this.player.video.duration);
        // console.log('loadeddataduration', duration);
        // console.log('loadeddataprogress', this.progress);
        if (this.restoreProgress && this.progress > 0) {
          this.restoreProgress = false;
          this.player.seek(this.progress);
        }
      });
      this.player.on('ended', () => {
        this.$emit('ended');
      });
      this.player.on('seeking', () => {
        const currentTime = moment.format(this.player.video.currentTime);
        // console.log('seekingcurrentTime', currentTime);
      });
      this.player.on('progress', () => {
        const currentTime = parseInt(this.player.video.currentTime);
        // console.log('currentTime', currentTime);
        if (currentTime) {
          this.$emit('onProgress', currentTime);
        }
      });
    }
  },
  data() {
    return {
      restoreProgress: true
    };
  }
};
</script>

<style lang="scss" scoped>
#dplayer {
  width: 100%;
  height: 100%;
}
</style>
