<template>
  <div class="comment-item" :data-id="comment.id">
    <div class="user-face">
      <a :href="`/user/${comment.user.id}`" :data-usercard-mid="comment.user.id">
        <div class="user-avatar">
          <img class="avatar-img" :src="comment.user.avatar" alt="" />
        </div>
      </a>
    </div>
    <div class="item-content">
      <div class="user-item">
        <div class="user-info">
          <a :href="`/user/${comment.user.id}`" class="name">{{ comment.user.name }}</a>
          <span class="time">{{ comment.time_ago }}</span>
        </div>
        <el-popover placement="bottom-end" trigger="hover" :show-arrow="false">
          <template #reference>
            <div class="operation">
              <svg
                t="1634525354353"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="2378"
              >
                <path
                  d="M146.030911 517.349845m-87.923492 0a87.923492 87.923492 0 1 0 175.846984 0 87.923492 87.923492 0 1 0-175.846984 0Z"
                  fill=""
                  p-id="2379"
                ></path>
                <path
                  d="M512 512m-87.923492 0a87.923492 87.923492 0 1 0 175.846984 0 87.923492 87.923492 0 1 0-175.846984 0Z"
                  fill=""
                  p-id="2380"
                ></path>
                <path
                  d="M873.565802 512m-87.923491 0a87.923492 87.923492 0 1 0 175.846983 0 87.923492 87.923492 0 1 0-175.846983 0Z"
                  fill=""
                  p-id="2381"
                ></path>
              </svg>
            </div>
          </template>
          <div class="operation-popover">
            <div v-if="user.id === comment.user.id" @click="onDelete" class="op-item">
              <span class="item-text"></span>
            </div>
            <div v-else @click="showReportModal" class="op-item">
              <span class="item-text"></span>
            </div>
          </div>
        </el-popover>
      </div>
      <p class="body-text">{{ comment.body }}</p>
      <div class="item-tools">
        <span :class="['tool-btn', comment.liked && 'on']" v-auth:click="likeHandler">
          <svg class="icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 36 36">
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M13.836 7C9.49 7 6 10.726 6 15.064c0 .108.002.194.003.253a1.62 1.62 0 00-.003.14c.013.582.146 1.284.293 1.87.143.572.344 1.208.57 1.668.907 1.952 2.495 3.846 4.023 5.402a46.034 46.034 0 005.138 4.51l.03.025.023.018c.339.34 1.055 1.05 2.331 1.05h.038c.361 0 1.224 0 1.996-.752l.063-.053.273-.227.006-.005c.847-.698 2.828-2.33 4.782-4.301 1.25-1.254 2.547-2.71 3.538-4.214.135-.204.265-.41.388-.617.049-.08.09-.166.123-.255.044-.115.065-.16.082-.194.019-.037.05-.093.131-.219.057-.089.106-.183.145-.281.055-.14.095-.235.127-.299l.006-.013a1.62 1.62 0 00.285-.562c.243-.863.41-1.623.426-2.558 0-.043 0-.086-.003-.129a10.365 10.365 0 00.003-.31C30.788 10.697 27.308 7 22.98 7c-1.641 0-3.238.508-4.498 1.532C17.119 7.545 15.547 7 13.836 7zm13.744 8.066v.042l-.002.15v.008l-.002.137c0 .015 0 .041.002.074-.014.44-.084.837-.233 1.402a3.667 3.667 0 00-.151.278c-.064.13-.122.265-.174.39a4.472 4.472 0 00-.376.738c-.076.125-.156.25-.24.378l-.002.002c-.823 1.25-1.954 2.534-3.13 3.713l-.003.003c-1.835 1.851-3.712 3.4-4.55 4.09l-.062.051-.235.195-.092-.091a3.812 3.812 0 00-.294-.254l-.012-.01a42.76 42.76 0 01-4.82-4.226l-.006-.006c-1.452-1.477-2.736-3.062-3.406-4.512a1.665 1.665 0 00-.021-.046c-.075-.15-.213-.533-.338-1.033a6.736 6.736 0 01-.193-1.074 1.837 1.837 0 000-.2v-.008a6.941 6.941 0 01-.003-.193c0-2.667 2.154-4.827 4.6-4.827 1.285 0 2.5.523 3.588 1.653a1.618 1.618 0 002.491-.193c.642-.914 1.732-1.46 3.064-1.46 2.444 0 4.6 2.162 4.6 4.827v.002z"
              fill-opacity="0.9"
            ></path>
          </svg>
          <span>{{ comment.likes }}</span>
        </span>
        <div class="split"></div>
        <span class="tool-btn" v-auth:click="toggleCommentEditor">
          <svg class="icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 36 36">
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M6 17.617C6 11.561 11.578 7 18 7s12 4.561 12 10.617c0 3.036-1.51 5.529-3.749 7.728-.187.184-.381.364-.58.54-1.925 2.082-3.911 3.417-5.847 3.973-.77.221-1.702.242-2.454-.381-.68-.564-.837-1.361-.878-1.823a3.848 3.848 0 01-.01-.136C10.898 26.91 6 23.264 6 17.617zM18 10c-5.177 0-9 3.602-9 7.617 0 3.867 3.794 6.992 9.152 6.992h1.5v1.717l-.06.206c0-.001-.03.104-.06.259 1.155-.46 2.515-1.394 3.984-2.996l.056-.06.063-.055c.182-.16.353-.318.514-.475C26.075 21.312 27 19.543 27 17.617 27 13.602 23.177 10 18 10z"
              fill-opacity="0.9"
            ></path>
          </svg>
          <span></span>
        </span>
      </div>
      <transition name="slide-fade">
        <div v-if="showCommentEditor" class="editor-container">
          <CommentEditor :commentable-id="commentableId" type="comments" @addComment="updateReplies" />
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import { defineComponent, toRefs, ref, reactive } from 'vue';
import { useMutation, useApolloClient } from '@vue/apollo-composable';
import { ElMessage } from 'element-plus';
import { exceptionCapture, errorMessage } from '@/common';
import { DELETE_COMMENT_MUTATION } from '@/graphql';
import CommentEditor from './CommentEditor.vue';

export default {
  components: {
    CommentEditor
  },
  props: {
    commentableId: {
      type: Number
    },
    comment: {
      type: Object
    }
  },
  setup(props, context) {
    const { comment } = toRefs(props);

    const { mutate: deleteComment } = useMutation(DELETE_COMMENT_MUTATION, () => ({
      variables: {
        id: comment.value.id
      }
    }));
    async function onDelete() {
      const [err, res] = await exceptionCapture(() => deleteComment());
      if (err) {
        ElMessage.error('');
      } else if (res?.data.deleteComment) {
        context.emit('deleteComment', comment.value);
        ElMessage({
          message: '',
          type: 'success'
        });
      }
    }

    function likeHandler() {}

    function updateReplies(comment) {
      context.emit('addComment', comment);
    }

    return {
      onDelete,
      likeHandler,
      updateReplies
    };
  },
  computed: {
    user() {
      return this.$store.state.user.user;
    }
  },
  methods: {
    showReportModal() {
      this.$bus.emit('SHOW_REPORT_MODAL', { id: this.comment.id, type: 'comments', title: '' });
    },
    toggleCommentEditor() {
      this.showCommentEditor = !this.showCommentEditor;
    }
  },
  data() {
    return {
      showCommentEditor: false
    };
  }
};
</script>

<style lang="scss" scoped>
@import './commentItem.scss';
</style>
