<template>
  <div class="comment-editor">
    <div v-if="!user.id" class="not-log">
      <div class="login" @click="showLoginModal"><span>&nbsp;&nbsp;</span></div>
    </div>
    <div v-else class="editor">
      <div class="editor-input">
        <div class="input-container">
          <input
            v-model="body"
            :disabled="loading"
            maxlength="120"
            class="input-root"
            type="text"
            placeholder=""
          />
        </div>
      </div>
      <div class="editor-toolbar">
        <div class="toolbar-menu">
          <span :class="['menu-item', body?.length > 0 ? 'active' : 'disabled']" @click="onSubmit">
            <svg class="icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 36 36">
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M17.5 30C23.851 30 29 24.851 29 18.5S23.851 7 17.5 7 6 12.149 6 18.5 11.149 30 17.5 30zm-5.16-13.883a1.16 1.16 0 001.64 1.64l2.395-2.394v8.838a1.16 1.16 0 002.321 0v-8.839l1.028 1.028 1.368 1.368a1.16 1.16 0 001.64-1.641l-4.219-4.22a1.382 1.382 0 00-1.954 0l-4.22 4.22z"
              ></path>
            </svg>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, ref, watch } from 'vue';
import { ElMessage } from 'element-plus';
import { useMutation } from '@vue/apollo-composable';
import { exceptionCapture, errorMessage } from '@/common';
import { ADD_COMMENT_MUTATION } from '@/graphql';

export default {
  props: {
    commentableId: Number,
    type: String
  },
  setup(props, context) {
    const { commentableId, type } = toRefs(props);
    const body = ref('');
    const flag = ref(false);

    const { mutate: addCommentMutate, loading } = useMutation(ADD_COMMENT_MUTATION, () => ({
      variables: {
        id: commentableId.value,
        type: type.value,
        body: body.value
      }
    }));

    async function onSubmit() {
      if (flag.value) {
        return;
      }
      flag.value = true;
      const [err, res] = await exceptionCapture(() => addCommentMutate());
      flag.value = false;
      if (err) {
        ElMessage.error('');
      } else if (res?.data.addComment) {
        body.value = '';
        context.emit('addComment', res?.data.addComment);
        ElMessage({
          message: '',
          type: 'success'
        });
      }
    }

    return {
      body,
      loading,
      addCommentMutate,
      onSubmit
    };
  },
  methods: {
    showLoginModal() {
      this.$bus.emit('SHOW_LOGIN_MODAL');
    }
  },
  computed: {
    user() {
      return this.$store.state.user.user;
    }
  },
  data() {
    return {};
  }
};
</script>

